* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

h1 {
  text-align: center;
}

.category-container {
  margin: auto;
  width: 70%;
  padding: 10px;
}

.category-container > select,
  button,
  input {
  margin: 5px;
  padding: 5px;
}

.container {
  display: flex;
  padding: 10px;
  flex-flow: row wrap;
  justify-content: space-between;
  margin: auto;
  width: 80%;
  background-color: black;
}

.container > p {
  color: white;
}

.cart-container {
  margin-top: 10px;
  margin-left: auto;
  margin-right: auto;
  width: 70%;
  color: black;
  border: 1px solid black;
}

